.animated {
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

@media (print), (prefers-reduced-motion: reduce) {
  .animated {
    -webkit-animation-duration: 0.2ms !important;
    animation-duration: 0.2ms !important;
    -webkit-transition-duration: 0.2ms !important;
    transition-duration: 0.2ms !important;
    -webkit-animation-iteration-count: 0.2 !important;
    animation-iteration-count: 0.2 !important;
  }
}